import React, { useEffect } from "react";
import { useState } from "react";
import {
  TextInput,
  Checkbox,
  Button,
  Group,
  Box,
  Select,
  LoadingOverlay,
  Modal,
} from "@mantine/core";
import { DatePicker } from "@mantine/dates";
import { useForm } from "@mantine/form";
import { useQuery } from "react-query";
import { makeGetRequest, sendData } from "../Api";
import axios from "axios";
export default function Home() {
  // - - - - - - - - - - - - - - - - - - - - - -
  const [tempUsers, setTempUsers] = useState([]);
  const [tempProjects, setTempProjects] = useState([]);
  const [visible, setVisible] = useState(false);
  const [modal, setModal] = useState({ visible: false, state: "pending" });

  const { data } = useQuery(
    ["users"],
    () => {
      return makeGetRequest("users");
    },
    {
      refetchOnWindowFocus: false,
    }
  );
  const { data: projects } = useQuery(
    ["peojects"],
    () => {
      return makeGetRequest("projects");
    },
    {
      refetchOnWindowFocus: false,
    }
  );

  useEffect(() => {
    if (data) {
      let newArr = [];
      data.forEach((element) => {
        newArr.push({ value: element.ID, label: element.name });
      });
      setTempUsers(newArr);
    }
    if (projects) {
      let newArr = [];
      projects.forEach((element) => {
        newArr.push({ value: element.ID, label: element.name });
      });
      setTempProjects(newArr);
    }
  }, [data, projects]);
  const { reset } = useForm();
  const form = useForm({
    initialValues: {
      desc: "",
      user: "",
      project: "",
      title: "",
      date: "",
    },

    validate: {
      user: (value) => (value != "" ? null : "Invalid"),
      desc: (value) => (value != "" ? null : "Invalid"),
      title: (value) => (value != "" ? null : "Invalid"),
      project: (value) => (value != "" ? null : "Invalid"),
      date: (value) => (value != "" ? null : "Invalid"),
    },
  });

  const handleValues = (e) => {
    setVisible(true);
    let newObj = {
      summary: e.title,
      desc: e.desc,
      issueType: "Task",
      projectId: e.project,
      reporterId: 1,
      assigneeId: e.user,
      priority: "Maybe",
      stepId: 2,
      dueDate: e.date,
      closed: false,
    };
    sendData(newObj, setVisible, setModal);
    form.reset();
  };

  return (
    <div className="home-page">
      <LoadingOverlay visible={visible} overlayBlur={2} />
      <Modal
        overlayBlur={5}
        centered
        opened={modal.visible}
        onClose={() => setModal({ ...modal, visible: false })}
        title={modal.state}
      >
        {/* Modal content */}
      </Modal>

      <div className="input-wrapper">
        <Box sx={{ maxWidth: 300 }} mx="auto">
          <form onSubmit={form.onSubmit(handleValues)}>
            <Select
              {...form.getInputProps("user")}
              data={tempUsers}
              label="Users"
              mt="md"
              size="md"
              withAsterisk
            />
            <Select
              {...form.getInputProps("project")}
              data={tempProjects}
              label="Projects"
              mt="md"
              size="md"
              withAsterisk
            />
            <TextInput
              mt="md"
              size="md"
              withAsterisk
              label="Title"
              placeholder="title"
              {...form.getInputProps("title")}
            />
            <TextInput
              mt="md"
              size="md"
              withAsterisk
              label="Description"
              placeholder="description"
              {...form.getInputProps("desc")}
            />
            <DatePicker
              mt="md"
              size="md"
              placeholder="Pick date"
              label="Event date"
              withAsterisk
              {...form.getInputProps("date")}
            />
            <Button fullWidth type="submit" mt={"md"}>
              Submit
            </Button>
          </form>
        </Box>
      </div>
    </div>
  );
}
