import MD5 from "crypto-js/md5";
import axios from "axios";

export const host = "http://tracker.lavina.tech";

export const handleError = (err) => {
  console.log("Ошибка", err);
};
export function makeGetRequest(reqName) {
  const headers = {
    // Authorization: "Bearer my-token",
    k: "rI4=oT8>nU0#dM8=wG0=zX8$kO2!yI",
    s: MD5("/" + reqName).toString(),
  };
  return axios
    .get(`http://tracker.lavina.tech/${reqName}`, { headers })
    .then((response) => response.data.data)
    .catch((err) => console.log(err));
}

export function sendData(body, loading, modal) {
  axios
    .post(host + "/issues", body, {
      headers: {
        k: "rI4=oT8>nU0#dM8=wG0=zX8$kO2!yI",
        s: MD5(JSON.stringify(body)).toString(),
      },
    })
    .then((res) => {
      loading(false);
      modal((prevState) => ({
        ...prevState,
        visible: true,
        state: "Muvaffaqqiyatli",
      }));
    })
    .catch((err) => {
      loading(false);
      modal((prevState) => ({
        ...prevState,
        visible: true,
        state: "Bajarilmadi",
      }));
    });
}
